<template>
    <div>
        <form style="width: 100%" @submit.prevent="submit">
        <CRow class="mb-3 ml-2">
            <h3>환자정보 수정</h3>
        </CRow>
        <CRow style="width: 80%">
            <table class="table table-striped" style="margin-left: 3%; border-collapse: collapse">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td class="col-title">
                        <span class="table-span-margin">
                            <strong>이름</strong>
                        </span>
                    </td>
                    <td style="width: 30%">
                        <CInput v-model="custItem.name"/>
                    </td>
                    <td class="col-title">
                        <span class="table-span-margin">
                            <strong>진료번호</strong>
                        </span>
                    </td>
                    <td style="width: 30%">
                        <CInput v-model="custItem.registNo"/>
                    </td>
                         </tr>
                         <tr>
                             <td class="col-title">
                                 <span class="table-span-margin">
                                     <strong>상태</strong>
                                 </span>
                             </td>
                             <td style="width: 30%">
                                 <select v-model="selected" class="form-control">
                                     <option value="I">입원</option>
                                     <option value="O">퇴원</option>
                                 </select>
                             </td>
                             <td class="col-title">
                                 <span class="table-span-margin">
                                     <strong>등록일</strong>
                                 </span>
                             </td>
                             <td style="width: 30%">
                                 <CInput v-model="preInsertDate" readonly=""/>
                             </td>
                         </tr>
                         <tr>
                             <td class="col-title">
                                 <span class="table-span-margin">
                                     <strong>입원일</strong>
                                 </span>
                             </td>
                             <td style="width: 30%">
                                 <input type="text" v-model="preInDate">
                                 <input type="date" class="form-control" v-model="preInDate"/>
                             </td>
                             <td class="col-title">
                                 <span class="table-span-margin">
                                     <strong>퇴원일</strong>
                                 </span>
                             </td>
                             <td style="width: 30%">
                                 <input type="text" v-model="preOutDate">
                                 <input type="date" class="form-control" v-model="preOutDate"/>
                             </td>
                         </tr>
                         <tr>
                             <td class="col-title">
                                 <span class="table-span-margin">
                                     <strong>미납액</strong>
                                 </span>
                             </td>
                             <td>
                                 <CInput style="width: 80%" v-model="custItem.charge" readonly=""/>
                             </td>
                             <td class="col-title" colspan="2">
                                 <span>
                                    <router-link :to="{name: 'paymanageunpaid', params: {custId: custItem.id}}" tag="button" class="form-control">
                                        미납액 차감
                                    </router-link>
                                 </span>
                            </td>
                         </tr>
                </tbody>
            </table>
        </CRow>
        <CRow style="margin-bottom: 3%;">
            <CCol class="btn-col-margin ml-4" sm="1">
                <button label=""
                        type="button"
                        class="btn btn-info btn-block"
                        @click="submit">
                    저 장</button>
            </CCol>
        </CRow>
        <CRow>
            <CCol>
                <CCard>
                    <CCardHeader>
                        <div class="card-header-actions">
                            <a
                                    href="https://coreui.io/vue/docs/components/tabs"
                                    class="card-header-action"
                                    rel="noreferrer noopener"
                                    target="_blank"
                            >
                            </a>
                        </div>
                    </CCardHeader>
                    <CCardBody>
                        <CTabs>
                            <CTab title="청구내역" active>
                                <CRow>
                                    <table class="table table-striped" style="border-collapse: collapse">
                                        <thead style="text-align: center">
                                            <th width="40%">청구일</th>
                                            <th width="30%">항목</th>
                                            <th width="30%">금액</th>
                                        </thead>
                                        <tbody class="position-relative">
                                            <tr v-for="i in this.custChargeItem">
                                                <td> {{$moment(i.insertTime).format('YYYY-MM-DD')}}</td>
                                                <td> {{i.item}}</td>
                                                <td> {{i.amount}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </CRow>
                                <CRow style="margin-top: 3%;">
                                    <CCol class="btn-col-margin ml-4" sm="1">
                                        <button label=""
                                                type="button"
                                                class="btn btn-info btn-block"
                                                @click="$router.push({name: 'paymanagecharge'})"
                                        >
                                            금액청구</button>
                                    </CCol>
                                </CRow>
                            </CTab>

                            <CTab title="납부내역" >
                                <CRow>
                                    <table class="table table-striped" style="border-collapse: collapse">
                                        <thead style="text-align: center">
                                            <th width="30%">납부일</th>
                                            <th width="30%">납부구분</th>
                                            <th width="20%">금액</th>
                                            <th width="20%">비고(납부자)</th>
                                        </thead>
                                        <tbody class="position-relative">
                                            <tr v-for="p in this.custPayItem">
                                                <td> {{$moment(p.insertTime).format('YYYY-MM-DD')}}</td>
                                                <td> {{p.payType}}</td>
                                                <td> {{p.amount}}</td>
                                                <td> {{p.userInfo}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </CRow>
                            </CTab>
                        </CTabs>
                    </CCardBody>
                </CCard>
            </CCol>
        </CRow>
        </form>
    </div>
</template>
<style>
    .btn-col-margin{
        margin-top: 5px;
        margin-left: 15px;
        margin-right: 10px;
    }
    .col-title{
        width: 20%;
    }
    .table-span-margin{
        margin-left: 10%;
    }
</style>
<script>
    import Vue from 'vue'
    import axios from "axios";
    import VueMoment from 'vue-moment'

    Vue.use(VueMoment)

    export default {
        name: 'paymanageupdate',
        components: {},
        data() {
            return{
                custItem: null,
                custPayItem: null,
                custChargeItem: null,
                payItems: null,
                item: null,
                preInDate: '',
                preOutDate: '',
                preInsertDate: '',
                chargeDate: '',
                selected: ''
            }
        },
        mounted() {
            const custId = this.$route.params.custId;

            axios.get('silver/cust/update/'+custId)
                .then(res => {
                    this.custItem = res.data.cust;
                    this.custPayItem = res.data.custPay;
                    this.custChargeItem = res.data.custCharge;
                    this.preInDate = res.data.cust.inDate.slice(0, 10);
                    this.preOutDate = res.data.cust.outDate.slice(0, 10);
                    this.preInsertDate = res.data.cust.insertDate.slice(0, 10);
                    this.selected = res.data.cust.custStatus;
                    console.log("custPayItem")
                    console.log(this.custPayItem)
                    console.log("custChargeItem")
                    console.log(this.custChargeItem)
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        methods: {
            submit () {
                axios.post('/silver/cust/custUpdate', {
                    id: this.$route.params.custId,
                    name: this.custItem.name,
                    registNo: this.custItem.registNo,
                    custStatus: this.selected,
                    inDate: this.preInDate,
                    outDate: this.preOutDate,
                    charge: this.custItem.charge
                })
                    .then(function (res) {
                        console.log(res.data)
                    })
                    .catch(function (error) {
                        console.log(error)
                    });
            }
        }
    }
</script>
